.wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}

.title {
  font-size: 1.5rem;
}

.description {
  font-size: 1.2rem;
  color: var(--secondary-color);

  &:hover {
    text-decoration: underline;
  }
}

.input-field {
  width: 100%;
  height: 44px;
  margin-bottom: 10px;
  background-color: #1618230f;
  display: flex;
  align-items: center;
  border: 1px solid #1618231f;
  border-radius: 2px;
}

.eye-icon {
  margin-right: 16px;
  font-size: 1.6rem;
  user-select: none;

  &:hover {
    cursor: pointer;
  }
}

.input {
  width: 100%;
  height: 100%;
  color: var(--black);
  padding: 0 12px;

  &::placeholder {
    opacity: 0.7;
  }
}

.error-message {
  color: red;
  display: none;
}

.active {
  display: block;
}
